$(window).on("scroll", function() {
  if (window.pageYOffset > $('#citation').position().top) {
    $('#navigation').addClass('bg-white');
    $('#navigation').addClass('shadow-sm');
  } else {
    $('#navigation').removeClass('bg-white');
    $('#navigation').removeClass('shadow-sm');
  }
  if ((window.pageYOffset +window.innerHeight) > ($('#donate').position().top) + $('#donation-title').height()) {
    $('.donate-animation').removeClass('invisible');
    $('.donate-animation').addClass('animate__animated animate__bounceInDown animate__slow')
  }
});