var container = $('.carousel-container');
var carouselMenu = $('.carousel-menu');
var carouselMenuItem = $('.carousel-menu-item');

var carouselMainTitle = $('#carousel-main-title');
var carouselMainText = $('#carousel-main-text');
var carouselSignature = $('#carousel-signature');
var carouselSecondText = $('#carousel-second-text');
var carouselMenuIcon = $('.icon-title');

var equi = {
  0: "familles",
  1: "personnesalarue",
  2: "personnesagees",
  3: "reveillon"
}


$(document).ready(function(){
  $('.slick-carousel').slick({
    arrows: false,
    dots: true,
    infinite: false
  });
});

carouselMenuItem.each(function() {
  $(this).click(function() {
    
    container.animate({
      left: "-175%",
      opacity: 0
    }, 1250);
    container.animate({
      left: "100%"
    }, 50);

    var data = carouselContent[$(this).attr('id')];
    $('#carousel-desktop').carousel(data['value']);
    $('#carousel-mobile').carousel(data['value']);
    $('.slick-carousel').slick('slickGoTo', data['value']);
    setTimeout(function(){
      carouselMainTitle.html(data['carousel-main-title']);
      carouselMainText.html(data['carousel-main-text']);
      carouselSignature.html(data['carousel-signature']);
      carouselSecondText.html(data['carousel-second-text']);
      carouselMenuIcon.attr('src', data['icon']);
    }, 1000);
    
    container.animate({
      left: 0,
      opacity: 1
    }, 1250);
    
    // change menu item state
    carouselMenuItem.each(function() {
      $(this).removeClass('active');
    })
    $(this).addClass('active')
  })
});

$('.slick-carousel').on('afterChange', function(event, slick, currentSlide){
    carouselMenuItem.each(function() {
      $(this).removeClass('active');
    })
    var element = $("#" + equi[currentSlide]);
    element.addClass('active')
});

(function() {
  'use strict';

  /**
   * Slide to item.
   * @param {object} event - Carousel slide event.
   * @param {string} [direction=left]- Cycle direction.
   */
  function slideTo(event, direction) {
    var $element = $(event.currentTarget);
    var $indicators = $element.find('.carousel-indicators');
    var $items = $element.find('.carousel-item');
    var $active = $element.find('.carousel-item.active');
    var $item = $(event.relatedTarget);
    var itemIndex = $item.index();
    
    if (typeof direction === 'undefined') direction = 'left';

    if (event.direction !== direction) {
      event.preventDefault();

      if (direction === 'right') {
        $item.insertBefore($active);
        $element.carousel('prev');
        
        setTimeout(function() {
          if (itemIndex === $items.length - 1) {
            $item.insertAfter($items.eq(itemIndex - 1));
          } else {
            $item.insertBefore($items.eq(itemIndex + 1));
          }
        }, 50);
      } else {
        $item.insertAfter($active);
        $element.carousel('next');
        
        setTimeout(function() {
          if (itemIndex === 0) {
            $item.insertBefore($items.eq(1));
          } else {
            $item.insertAfter($items.eq(itemIndex - 1));
          }
        }, 50);
      }

      $indicators.find('.active').removeClass('active');
      $indicators.children().eq(itemIndex).addClass('active');
    }
  }

  $('#carousel-desktop').on('slide.bs.carousel', function(event) {
    slideTo(event, 'left');
  });

})();